.kv {
  width: 100%;
  height: 300px;
  background: #000;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.links {
  margin: 40px 0;
}
.links ul {
  display: flex;
  justify-content: center;
}
.links ul li:not(:last-child) {
  margin-right: 30px;
}
.links ul li a {
  font-size: 20px;
  text-decoration: underline;
}